import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
// import { FirebaseAuth, OrdinaryComponent } from 'private-angular-vhframeworks';
@Component({
  selector: 'app-modal-checklDealer',
  templateUrl: './modal-edit-check-ldealer.component.html',
  styleUrls: ['./modal-edit-check-ldealer.component.scss']
})
export class ModalEditCheckLDealerComponent implements OnInit {
  form: FormGroup;

  constructor(
    // public firebaseAuth: FirebaseAuth,
    // public ordinaryComponent:OrdinaryComponent,
    public dialog: MatDialog,
  ) { }

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.form = new FormGroup({
      name: new FormControl('', [Validators.required]),
      type: new FormControl('', [Validators.required, Validators.email]),
      phone: new FormControl('', [Validators.required]),
      password: new FormControl('', [Validators.required]),
      date: new FormControl(new Date()),
    });
  }

  task: any = {
    name: 'Chọn tất cả',
    completed: false,
    color: 'primary',
    subtasks: [
      {name: 'Viethas', completed: false, color: 'primary'},
     
    ],
  };

  allComplete: boolean = false;

  updateAllComplete() {
    this.allComplete = this.task.subtasks != null && this.task.subtasks.every(t => t.completed);
  }

  someComplete(): boolean {
    if (this.task.subtasks == null) {
      return false;
    }
    return this.task.subtasks.filter(t => t.completed).length > 0 && !this.allComplete;
  }

  setAll(completed: boolean) {
    this.allComplete = completed;
    if (this.task.subtasks == null) {
      return;
    }
    this.task.subtasks.forEach(t => (t.completed = completed));
  }
}
