import { DeleteDealerComponent } from './delete-dealer/delete-dealer.component';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
// import { FirebaseAuth, QueryVhAPP } from 'private-angular-vhframeworks';
import { EditDealerComponent } from './edit-dealer/edit-dealer.component';
import { AddDealerComponent } from './add-dealer/add-dealer.component';



@Component({
  selector: 'app-dealer',
  templateUrl: './dealer.component.html',
  styleUrls: ['./dealer.component.scss'],
})
export class DealerComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  /// panigatior ///
  pageIndex: number = 0;
  pageSize: number = 10;
  lowValue: number = 0;
  highValue: number = 10;
  pageSizeOption: any = [10, 25, 50, 100];
  startTime: any = new Date();
  endTime: any = new Date();
  list_delear: any = [];
  constructor(
    // private queryVhAPP: QueryVhAPP,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth
  ) {}

  ngOnInit(): void {
    this.getDealer();
  }

  getDealer() {
    // this.firebaseAuth.getDealers().then((data) => {
    //   if (data) this.list_delear = data;
    // });

    
  }

  openAddDealer() {
    const dialogRef = this.dialog.open(AddDealerComponent, {
      width: '55%',
      maxWidth: '100%',
    });
    

    dialogRef.afterClosed().subscribe((result) => {
      this.getDealer();
    });
  }
  opeEditDealer(item) {
    const dialogRef = this.dialog.open(EditDealerComponent, {
      width: '55%',
      maxWidth: '100%',
      data: item,
    });

    dialogRef.afterClosed().subscribe((result) => {
      this.getDealer();
    });
  }
  openDeleteDealer(item) {

  }


  // slice panigator
  getPaginatorData(event: PageEvent): PageEvent {
    this.lowValue = event.pageIndex * event.pageSize;
    this.highValue = this.lowValue + event.pageSize;
    return event;
  }
}
