import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
// import { FirebaseAuth, QueryVhAPP } from 'private-angular-vhframeworks';
import { AddDiscountComponent } from './add-discount/add-discount.component';
import { EditDiscountComponent } from './edit-discount/edit-discount.component';

@Component({
  selector: 'app-discount',
  templateUrl: './discount.component.html',
  styleUrls: ['./discount.component.scss']
})
export class DiscountComponent implements OnInit {

  @ViewChild(MatPaginator) paginator: MatPaginator;
  /// panigatior ///
  pageIndex: number = 0;
  pageSize: number = 10;
  lowValue: number = 0;
  highValue: number = 10;
  pageSizeOption: any = [10, 25, 50, 100];
  startTime: any = new Date();
  endTime: any = new Date();
  list_employee: any = [];
  constructor(
    // private queryVhAPP: QueryVhAPP,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth
  ) {}

  ngOnInit(): void {
    this.getEmployee();
  }

  getEmployee() {
    // this.firebaseAuth.getEmployees().then((data) => {
    //   if (data) this.list_employee = data;
    // });

    
  }

  openAddDiscount() {
    const dialogRef = this.dialog.open(AddDiscountComponent, {
      width: '55%',
      maxWidth: '100%',
    });
    

    dialogRef.afterClosed().subscribe((result) => {
      this.getEmployee();
    });
  }
  openEditDiscount() {
    const dialogRef = this.dialog.open(EditDiscountComponent, {
      width: '55%',
      maxWidth: '100%',
      // data: item,
    });

    dialogRef.afterClosed().subscribe((result) => {
      this.getEmployee();
    });
  }
  openDeleteCommission() {
   
  }


  // slice panigator
  getPaginatorData(event: PageEvent): PageEvent {
    this.lowValue = event.pageIndex * event.pageSize;
    this.highValue = this.lowValue + event.pageSize;
    return event;
  }

}
