import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DealerComponent } from './dealer.component';
import { Routes, RouterModule } from '@angular/router';
import { MaterialModule } from 'src/app/dealer/material.module';
import { EditDealerComponent } from './edit-dealer/edit-dealer.component';
import { DeleteDealerComponent } from './delete-dealer/delete-dealer.component';
import { CommissionDealerComponent } from './commission-dealer/commission-dealer.component';
import { ModalAddCommissionComponent } from './commission-dealer/modal-add-commission/modal-add-commission.component';
import { ModalEditCommissionComponent } from './commission-dealer/modal-edit-commission/modal-edit-commission.component';
import { ModalDeleteCommissionComponent } from './commission-dealer/modal-delete-commission/modal-delete-commission.component';
import { ModalCheckLDealerComponent } from './commission-dealer/modal-check-ldealer/modal-check-ldealer.component';
import { ModalEditCheckLDealerComponent } from './commission-dealer/modal-edit-check-ldealer/modal-edit-check-ldealer.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { AddDealerComponent } from './add-dealer/add-dealer.component';
import { DiscountComponent } from './discount/discount.component';
import { AddDiscountComponent } from './discount/add-discount/add-discount.component';
import { EditDiscountComponent } from './discount/edit-discount/edit-discount.component';

const routes: Routes = [
  {
    path: '',
    component:DealerComponent,
  },
  {
    path: 'discount',
    component:DiscountComponent,
  }
 
];


@NgModule({
  declarations: [DealerComponent,CommissionDealerComponent,
     EditDealerComponent, DeleteDealerComponent, DiscountComponent,
      ModalAddCommissionComponent, ModalEditCommissionComponent, ModalDeleteCommissionComponent, 
      ModalCheckLDealerComponent, ModalEditCheckLDealerComponent, AddDealerComponent, DiscountComponent, AddDiscountComponent, EditDiscountComponent],
  imports: [
    CommonModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes),
  ]
})
export class DealerModule { }
