@Component({
  selector: 'app-dealer',
  templateUrl: './commission-dealer.component.html',
  styleUrls: ['./commission-dealer.component.scss'],
})
export class CommissionDealerComponent implements OnInit {
  @ViewChild(MatPaginator) paginator: MatPaginator;
  /// panigatior ///
  pageIndex: number = 0;
  pageSize: number = 10;
  lowValue: number = 0;
  highValue: number = 10;
  pageSizeOption: any = [10, 25, 50, 100];
  startTime: any = new Date();
  endTime: any = new Date();
  list_employee: any = [];
  constructor(
    // private queryVhAPP: QueryVhAPP,
    public dialog: MatDialog,
    // public firebaseAuth: FirebaseAuth
  ) { }

  ngOnInit(): void {
    this.getEmployee();
  }

  getEmployee() {
    // this.firebaseAuth.getEmployees().then((data) => {
    //   if (data) this.list_employee = data;
    // });


  }
  openAddEMployee() { }

  openAddDealer() {
    const dialogRef = this.dialog.open(ModalAddCommissionComponent, {
      width: '55%',
      maxWidth: '100%',
    });


    dialogRef.afterClosed().subscribe((result) => {
      this.getEmployee();
    });
  }
  opeEditDealer(item) {
    const dialogRef = this.dialog.open(ModalEditCommissionComponent, {
      width: '55%',
      maxWidth: '100%',
      data: item,
    });

    dialogRef.afterClosed().subscribe((result) => {
      this.getEmployee();
    });
  }
  openDeleteDealer(item) {
    const dialogRef = this.dialog.open(ModalDeleteCommissionComponent, {
      width: '767px',
      height: '261px',
      minHeight: '261px',
      maxWidth: '100%',
      data: item,
    });

    dialogRef.afterClosed().subscribe((result) => {
      this.getEmployee();
    });
  }

  openEditCommision() {

  }
  openDeleteCommission() {

  }
  // slice panigator
  getPaginatorData(event: PageEvent): PageEvent {
    this.lowValue = event.pageIndex * event.pageSize;
    this.highValue = this.lowValue + event.pageSize;
    return event;
  }
}
