import { Component, Inject, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from '@angular/material/dialog';
// import { QueryVhAPP, FirebaseAuth } from 'private-angular-vhframeworks';

@Component({
  selector: 'app-edit-empployee',
  templateUrl: './edit-dealer.component.html',
  styleUrls: ['./edit-dealer.component.scss'],
})
export class EditDealerComponent implements OnInit {
  form: FormGroup;
  constructor(
    // private queryVhAPP: QueryVhAPP,
    // public firebaseAuth: FirebaseAuth,
    public dialog: MatDialog,
    // public dialogRef: MatDialogRef<EditEmpployeeComponent>,
    @Inject(MAT_DIALOG_DATA) public employee: any
  ) {}

  ngOnInit(): void {
    this.initForm(this.employee);
  }

  initForm(data) {
    this.form = new FormGroup({
      name: new FormControl(data.name, [Validators.required]),
      email: new FormControl({value:data.email, disabled: true}, [Validators.required,Validators.email]),
      phone: new FormControl(data.phone, [Validators.required]),
      password: new FormControl(data.password, [Validators.required]),
      date: new FormControl(new Date()),
    });
  }
  updateEmployee() {
    // this.firebaseAuth.updateEmployee(this.employee._id, this.form.value).then((data)=>{});
  }
}
